.header {
	padding-top: 6px;
	&__wrapper {
		position: relative;
		@include clr; }
	&__left {
		float: left; }
	&__nav {
		max-width: 1270/1840*100 + %;
		padding-top: 20px;
		@include ld {
			padding-top: 8px; }
		@include lg {
			padding-top: 15px; }
		@include dt {
			padding-top: 9px; }
		@include m {
			position: absolute;
			width: 257px;
			right: -12px;
			background: $blue-d;
			top: 55px;
			padding-top: 0;
			max-width: 100%; } }
	&__right {
		position: absolute;
		top: 20px;
		right: 0;
		@include ld {
			top: 12px; }
		@include dt {
			top: 9px; }
		@include m {
			position: relative;
			top: 0;
			float: right;
			margin-right: 80px;
			padding-top: 4px; } }
	&__loc {
		// margin-top: 8px
		margin-right: 46px;
		@include ld {
			margin-right: 40px; }
		@include dt {
			margin-right: 20px; }
		@include ds {
			margin-right: 10px; }
		@include r(1250) {
			margin-right: 25px; }
		@include m {
			display: none; }
		//+dt
 }		//	margin-top: 2px
	&__nav-btn {
		display: none;
		cursor: pointer;
		position: absolute;
		padding-top: 6px;
		@include trans;
		top: 8px;
		right: 12px;
		width: 40px;
		height: 20px;
		border-top: 3px solid $blue-d;
		border-bottom: 3px solid $blue-d;
		@include m {
			display: block; }
		span {
			display: block;
			height: 3px;
			background: $blue-d;
			@include trans; }
		&.is-active {
			border-top: 3px solid $orange;
			border-bottom: 3px solid $orange;
			span {
				background: $orange; } }
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			right: -5px;
			bottom: -5px;
			left: -5px; } } }
.ndropdown {
	z-index: 2;
	position: absolute;
	padding: 18px 30px;
	top: 105%;
	box-shadow: $sdb;
	width: 1530px;
	left: 0;
	font-size: 0;
	@include ld {
		width: 1200px; }
	@include dm {
		width: 830px; }
	@include m {
		position: relative;
		top: 0;
		width: 100%;
		padding: 0; }
	&__wrapper {
		@include clr; }
	&__left {
		width: 875/1470*100 + %;
		float: left;
		@include m {
			float: none;
			width: 100%; } }
	&__right {
		width: 545/1470*100 + %;
		float: right;
		position: relative;
		@include cover;
		height: 485px;
		@include ld {
			height: 378px; }
		@include dm {
			height: 265px; }
		@include m {
			display: none; }
		svg {
			fill: $blue;
			font-size: 20px;
			position: absolute;
			left: -55px;
			top: 43%;
			margin-top: -10px;
			@include ld {
				font-size: 16px;
				left: -42px;
				margin-top: -8px; }
			@include dm {
				font-size: 13px;
				left: 27px; } } }
	&__btn {
		position: absolute;
		bottom: 35px;
		left: 50%;
		transform: translateX(-50%);
		@include ld {
			bottom: 28px; }
		@include dm {
			bottom: 18px; } } }
.dropitem {
	width: 25%;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	margin-bottom: 25px;
	@include dm {
		margin-bottom: 15px; }
	@include m {
		display: block;
		width: 100%;
		margin-bottom: 0; }
	&--last-row {
		margin-bottom: 0;
		@include dm {
			margin-bottom: 0; } }
	&__img {
		width: 184px;
		height: 169px;
		margin: 0 auto 5px;
		img {
			object-fit: cover;
			height: 100%;
			width: 100%; }
		@include ld {
			width: 144px;
			height: 136px; }
		@include dm {
			width: 100px;
			height: 90px; }
		@include m {
			display: none; } }
	&__text {
		color: $blue;
		font-size: 24px;
		line-height: 1.3;
		@include ld {
			font-size: 18px; }
		@include dm {
			font-size: 15px; }
		@include m {
			display: block;
			text-align: left;
			padding: 10px  10px 10px 30px;
			background: $blue-lt;
			color: $white;
			font-size: 20px; } } }
.discount {
	position: relative;
	background: $blue-d;
	width: 425px;
	margin-top: 38px;
	padding: 18px 18px 29px 18px;
	@include ld {
		width: 300px;
		padding: 12px 10px 25px 10px; }
	@include dm {
		margin-top: 20px;
		width: 230px;
		padding: 7px 10px 14px 10px; }
	&:before {
		content: '';
		position: absolute;
		width: 86px;
		height: 86px;
		border-right: 9px solid  $orange;
		border-bottom: 9px solid  $orange;
		bottom: -9px;
		right: -9px;
		@include ld {
			width: 68px;
			height: 68px;
			border-right: 7px solid  $orange;
			border-bottom: 7px solid  $orange;
			bottom: -7px;
			right: -7px; }
		@include dm {
			width: 46px;
			height: 46px;
			border-right: 5px solid  $orange;
			border-bottom: 5px solid  $orange;
			bottom: -5px;
			right: -5px; } }

	&__sec-text, &__main-text {
		color: $white; }
	&__main-text {
		font-weight: 700;
		font-size: 30px;
		@include ld {
			font-size: 24px; }
		@include dm {
			font-size: 17px; }
		span {
			font-size: 36px;
			@include ld {
				font-size: 28px; }
			@include dm {
				font-size: 20px; } } }
	&__sec-text {
		font-size: 24px;
		font-weight: 400;
		@include ld {
			font-size: 18px; }
		@include dm {
			font-size: 14px; } } }
